<template>

    <el-table
            ref="multipleTable"
            :data="checkedPlugins"
            tooltip-effect="dark"
            style="width: 100%">
        <el-table-column
                type="selection"
                width="55">
        </el-table-column>
        <el-table-column
                label="Plugin"
                width="120">
            <template slot-scope="scope">{{ scope.row.name }}</template>
        </el-table-column>
        <el-table-column
                prop="comment"
                label="comment"
                width="400">
        </el-table-column>
    </el-table>
</template>
<script>
    export default {
        name: 'Plugin',
        data() {
            return {
                checkedPlugins: [
                    {
                        name: 'Support',
                        comment: 'Transparent get into root shell\n'
                    },
                    {
                        name: 'Tail',
                        comment: 'A Unix-like command to flush logs\n'
                    },
                    {
                        name: 'Repeat',
                        comment: 'A suite of commands execute periodically\n'
                    },
                ],
                multipleSelection: []
            }
        },
        methods: {},
        mounted() {
            this.$refs.multipleTable.toggleAllSelection();
        }
    }
</script>


