                </div>
            </el-col>
        </el-row>
        <el-divider></el-divider>
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <el-table
                            v-loading="loading"
                            :data="tableData"
                            style="width: 100%"
                            :default-sort="{prop: 'port'}"
                    >
                        <el-table-column
                                prop="port"
                                label="port"
                                sortable
                                width="100">
                        </el-table-column>
                        <el-table-column
                                prop="device"
                                label="device"
                                sortable
                                width="120">
                        </el-table-column>
                        <el-table-column
                                prop="connect"
                                label="connect"
                                sortable
                                width="200">
                        </el-table-column>
                        <el-table-column
                                prop="in"
                                label="in"
                                sortable
                                width="120">
                        </el-table-column>
                        <el-table-column
                                prop="out"
                                label="out"
                                sortable
                                width="120">
                        </el-table-column>
                        <el-table-column
                                prop="status"
                                label="status"
                                sortable
                                width="150">
                        </el-table-column>
                        <el-table-column
                                label="action"
                                width="200">
                            <template slot="header" slot-scope="scope">
                                <el-button @click="restart">disconnect all</el-button>
                            </template>
                            <template slot-scope="scope">
                                <el-button @click="disconnect(scope.row.port)">disconnect</el-button>
                                <el-button @click="download(scope.row.port)">crt_link</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-col>
        </el-row>
    </div>

