    export default {
        name: 'Status',
        data() {
            return {
                tableData: [],
                loading: true,
                status: ''
            }
        },
        methods: {
            get_devices() {
                this.loading = true;
                let self = this;
                this.axios.get('api/v1/ser2net/status')
                    .then(function (response) {
                        self.status = response.data;
                    }).catch(function (error) {
                    self.$message.error('Failed to get ser2net status');
                });
                this.axios.get('api/v1/device/status')
                    .then(function (response) {
                        if (response.data) {
                            self.loading = false;
                        }
                        self.tableData = response.data || [];
                    }).catch(function (error) {
                    self.loading = false;
                    self.$message.error('Failed to get device status');
                })
            },
            disconnect(port) {
                let data = {
                    port: port
                };
                let self = this;
                console.log(data);
                this.axios.post('api/v1/disconnect', data)
                    .then(function (response) {
                        self.$message({
                            message: 'success',
                            type: 'success'
                        });
                        self.get_devices();
                    }).catch(function (error) {
                    self.$message.error('Failed');
                });

            },
            restart() {
                let self = this;
                this.axios.post('api/v1/action', {'opmode': 'restart'})
                    .then(function (response) {
                        self.$message({
                            message: 'success',
                            type: 'success'
                        });
                        self.get_devices();
                    }).catch(function (error) {
                    self.$message.error('Failed');
                });
            },
