<template>
    <div id="app">
        <el-container>
            <el-header style="height: 150px">
<pre style="text-align: left"> _____                                           ______                            _
(_____)              _                  _       / _____)                          | |
   _    ____    ___ | |_   ____  ____  | |_    | /       ___   ____    ___   ___  | |  ____
  | |  |  _ \  /___)|  _) / _  ||  _ \ |  _)   | |      / _ \ |  _ \  /___) / _ \ | | / _  )
 _| |_ | | | ||___ || |__( ( | || | | || |__   | \_____| |_| || | | ||___ || |_| || |( (/ /
(_____)|_| |_|(___/  \___)\_||_||_| |_| \___)   \______)\___/ |_| |_|(___/  \___/ |_| \____)

</pre>
            </el-header>
            <el-container>
                <el-aside width="180px">
                    <el-menu
                            :default-active="activeIndex"
                            :router="true"
                            class="el-menu-vertical-demo"
                    >
                        <el-menu-item index="index">
                            <span slot="title">Index</span>
                        </el-menu-item>
                        <el-menu-item index="status">
                            <template slot="title">
                                <span>Status</span>
                            </template>
                        </el-menu-item>
                        <el-menu-item index="configuration">
                            <span slot="title">Configuration</span>
                        </el-menu-item>
                        <el-menu-item index="about">
                            <span slot="title">About</span>
                        </el-menu-item>
                    </el-menu>
                </el-aside>
                <el-main>
                    <router-view/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
            }
        },
        computed:{
            activeIndex: function () {
                let path = this.$route.path.slice(1);
                console.log(path);
                return path === '/' ? 'index' : path;
            }
        },
        methods: {
        },
    }
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }
</style>



