<template>
    <div>
        <el-form label-position="left" label-width="150px">
            <el-form-item label="Configuration">
                <el-steps :active="active" finish-status="success" align-center>
                    <el-step description="Detect physical port change">
                        <template slot="title">
                            <el-button round @click="confirm('detect')">Detect</el-button>
                        </template>
                    </el-step>
                    <el-step title="Generate" description="Generate and write config file">
                        <template slot="title">
                            <el-button @click="confirm('generate')" :disabled="ctl_write">Generate</el-button>
                        </template>
                    </el-step>
                    <el-step title="Reboot" description="Reboot console server to take effect">
                        <template slot="title">
                            <el-button @click="confirm('reboot')" :disabled="ctl_reboot">Reboot</el-button>
                        </template>
                    </el-step>
                </el-steps>
            </el-form-item>
            <el-form-item label="Console Server">
                <el-button @click="confirm('reboot')">Reboot</el-button>
            </el-form-item>
            <el-form-item label="KeepDays">
                <el-input-number v-model="keepday" :min="7" :max="200" label="KeepDays" disabled></el-input-number>
            </el-form-item>
        </el-form>
    </div>

</template>

<script>
    export default {
        name: 'Configuration',
        data() {
            return {
                keepday: 30,
                activeOpmode: '',
                fullscreenLoading: false,
                active: 0,
                ctl_reboot: true,
                ctl_write: true,
            }
        },
        methods: {
            confirm(opmode) {
                this.activeOpmode = opmode;
                console.log(opmode);
                let self = this;
                switch (opmode) {
                    case 'detect':
                        const loading = this.$loading({
                            lock: true,
                            text: 'Detecting',
                            spinner: 'el-icon-loading',
                            background: 'rgba(0, 0, 0, 0.7)'
                        });
                        this.axios.post('api/v1/action', {'opmode': 'detect'})
                            .then(function (response) {
                                let msg = response.data ? 'detect change' : 'nothing change';

                                if (response.data) {
                                    self.active = self.active === 0 ? 1 : self.active;
                                    self.ctl_write = false;
                                }

                                loading.close();
                                self.$message({
                                    message: msg,
                                    type: 'success'
                                });

                            }).catch(function (error) {
                            loading.close();
                            self.ctl_write = true;
                            self.ctl_reboot = true;
                            self.active = 0;
                            self.$message.error('Failed');
                        });
                        return;
                    case 'reboot':
                        this.$confirm("Are you sure to reload console server? " +
                            "All connection would disconnect. and need about 1 mins to recover",
                            'Confirm', {
                                confirmButtonText: 'Confirm',
                                cancelButtonText: 'Cancel'
                            })
                            .then(() => {
                                this.axios.post('api/v1/action', {'opmode': 'reboot'})
                                    .then(function (response) {
                                        console.log(response.data);
                                        self.$message({
                                            message: 'success',
                                            type: 'success'
                                        });
                                    }).catch(function (error) {
                                    self.$message.error('Failed');
                                });
                            });
                        break;
                    case 'generate':
                        this.$confirm("Are you sure to generate new config file " +
                            "You need reboot to take effect.",
                            'Confirm', {
                                confirmButtonText: 'Confirm',
                                cancelButtonText: 'Cancel'
                            })
                            .then(() => {
                                this.axios.post('api/v1/action', {'opmode': 'generate'})
                                    .then(function (response) {
                                        let msg = response.data ? 'write success' : 'nothing change';
                                        self.active = 0;
                                        if (response.data) {
                                            self.active = 2;
                                            self.ctl_reboot = false;
                                        }
                                        self.$message({
                                            message: msg,
                                            type: 'success'
                                        });
                                    }).catch(function (error) {
                                    self.$message.error('Failed');
                                });
                            });


                        break;
                    default:

                        return;
                }
            },
        }
    }
</script>




