<template>
    <el-table
            v-loading="loading"
            :data="tableData"
            style="width: 100%"
            :default-sort="{prop: 'port'}"
    >
        <el-table-column
                prop="port"
                label="port"
                sortable
                width="200">
        </el-table-column>
        <el-table-column
                prop="date"
                label="date"
                sortable
                width="150"
        >
        </el-table-column>
        <el-table-column
                prop="log"
                label="log"
                width="200"
        >
            <template slot="header" slot-scope="scope">
                <el-input
                        width="150"
                        v-model="filterlog"
                        size="mini"
                        placeholder="search">
                </el-input>
            </template>
            <template slot-scope="scope">
                <el-link :underline="true" type="success" :href="'log/'+ scope.row.log"
                         target="_blank">{{ scope.row.log }}
                </el-link>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
    export default {
        name: 'Index',
        data() {
            return {
                data: [],
                filterlog: '',
                loading: true,
            }
        },
        computed: {
            tableData: {
                set: function (msg) {
                    this.data.push(msg);
                },
                get: function () {
                    return this.data.filter(data => !this.filterlog || data.log.toLowerCase().includes(this.filterlog.toLowerCase()));
                }
            }
        },
        methods: {
            get_log() {
                let self = this;
                this.axios.get('api/v1/index')
                    .then(function (response) {
                        // console.log(response.data);
                        if (response.data) {
                            self.loading = false;
                            self.$message({
                                message: 'success',
                                type: 'success'
                            });
                        }
                        for (let t of response.data.file) {
                            self.tableData =
                                {
                                    port: t.slice(5, 9),
                                    date: t.slice(10, 18),
                                    log: t
                                }

                        }
                    }).catch(function (error) {
                    self.$message.error('Failed');
                    self.loading = false;
                })
            },
        },
        mounted() {
            this.get_log();
        }
    }
</script>




